body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin: 30px auto 0;
}

#loadHotels {
  margin-bottom: 30px;
}
.hotel-container {
    margin-bottom: 30px;
}

.hotel {
    display: flex;
    border: 1px solid #aaa;
    border-radius: 7px;
    overflow: hidden;
}

.hotel.reviews-display {
    border-radius: 7px 7px 0 0;
}

.hotel .photo{
    flex: 1 1;
    border-right: 1px solid #aaa;
    background: center center no-repeat;
}

.hotel .data {
    flex: 2 1;
    padding: 30px 30px 30px 20px;
    text-align: left;
    background-color: #f6f6f6;
}

.hotel .data .title {
    text-transform: capitalize;
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
}

.hotel .data .stars {
    color: #aaa;
    font-size: 1.5rem;
}

.hotel .data .star {
    color: #000;
}

.hotel .data .footer {
    margin-top: 30px;
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
}

.footer .action {
    align-self: center;
}

.footer .price {
    text-align: right;
}

.footer .price .value {
    font-weight: 500;
    font-size: 3rem;
    line-height: 1;
}

@media (max-width: 767px) {
    .hotel,
    .hotel .data .title,
    .hotel .footer {
        flex-direction: column;
    }

    .hotel .photo {
        border-right: 0;
        width: 100%;
        height: 200px;
    }

    .hotel .data .footer {
        flex-direction: column-reverse;
    }
}
.reviews-list {
    background-color: #bbb;
    border: 1px solid #aaa;
    border-top: 0;
    border-radius: 0 0 7px 7px;
    padding: 30px;
}

@media (max-width: 767px) {
    .reviews-list {
        padding: 30px 20px;
    }
}
.review {
    display: inline-flex;
    border-bottom: 1px solid #999;
    width: 100%;
    text-align: left;
    padding-bottom: 30px;
    margin-bottom: 30px;
}

.review:last-child {
    padding-bottom: 0;
    margin-bottom: 0;
    border: 0;
}

.review .type {
    position: relative;
    margin-right: 30px;
}

.review .type .icon {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #f6f6f6;
    position: relative;
    font-weight: 500;
    font-size: 1.5rem;
    line-height: 1;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.review .content {
    flex: 1 1;
}

@media (max-width: 767px) {
    .review .type {
        margin-right: 15px;
    }

    .review .type .icon {
        width: 30px;
        height: 30px;
        font-size: 1rem;
    }
}
